	module.exports = {"data":{"markdownRemark":{"html":"<h2>Circle</h2>\n<p>A circle is a closed shape where all points are the same distance from the center.</p>\n<p>The distance from one point to the center is called the radius (R).</p>\n<p>The distance from one point, throught the center to another point is called the diameter (D).</p>\n<p>The distance all around the circle is called the circumference (C). </p>\n<p><img src=\"https://upload.wikimedia.org/wikipedia/commons/thumb/0/03/Circle-withsegments.svg/612px-Circle-withsegments.svg.png\" alt=\"circle\" title=\"circle\"></p>\n<h4>More Information:</h4>\n<!-- Please add any articles you think might be helpful to read before writing the article -->\n<p>More information with illustrations can be found here: <a href=\"https://en.wikipedia.org/wiki/Circle\">https://en.wikipedia.org/wiki/Circle</a></p>","fields":{"slug":"/mathematics/circle/"},"frontmatter":{"title":"Circle"}}},"pathContext":{"slug":"/mathematics/circle/"}}

